form {
    position: absolute;
    width: 50%;
    height: 40%;
    left: 25%;
    top: calc(9vh + 30%);
    display: flex;
    flex-direction: column;
}

label {
    font-weight: bold;
    color: #442FE0;
    text-align: center;
}

input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: #442FE0 solid 1.5px;
}

textarea {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: #442FE0 solid 1.5px;
}

textarea:focus {
    background-color: rgba(68, 47, 224, 0.2);
}

input:focus {
    background-color: rgba(68, 47, 224, 0.2);
}

button {
    background-color: #442FE0 !important;
    border: none !important;
