@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap');

* {
    font-family: 'Open Sans', sans-serif;
}

.resume-main {
    margin-top: 9vh;
    margin-bottom: 9vh;
}

.resume-main h1 {
    font-size: 6vw;
    font-weight: 900;
    color: #442FE0;
    margin-left: 9vh;
    margin-top: 9vh;
}

.resume-main h2 {
    font-size: 5vw;
    font-weight: 700;
    color: #442FE0;
}

.resume-main-jobs {
    margin-top: 9vh;
    text-align: center;
}

.resume-main-employer {
    display: flex;
    flex-direction: column;
}

.resume-main-employer-job {
    display: flex;
    flex-direction: column;
}

.resume-main-employer-name {
    font-size: 3vw;
    font-weight: 700;
    color: #442FE0;
    margin-top: 5vh;
}

.resume-main-employer-job {
    margin: 2vh 0vh 0vh 0vh;
}

.resume-main-employer-job-title {
    margin: 0vh 0vh 2vh 0vh;
    color: #442FE0;
}

.resume-main-employer-job-details li {
    color: #818181;
    list-style: none;
    padding-bottom: 2vh;
}