@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap');

* {
    font-family: 'Open Sans', sans-serif;
}

.main-text {
    position: absolute;
    margin-top: 9vh;
    margin-left: 9vh;
}

.main-text h1 {
    font-size: 9vw;
    font-weight: 900;
    color: #442FE0;
}

.main-text h2 {
    font-size: 5vw;
    font-weight: 700;
    color: #442FE0;
}

.main-text {
    display: flex;
    flex-direction: column;
}

.main-text-details {
    margin-top: 7vh;
    display: flex;
    flex-direction: row;
    width: 60%;
}

.main-text-details span {
    padding-right: 5vw;
    color: #818181;
}

.main-text-details h3 {
    color: #FBDC00;
    font-weight: bold;
}

#roman-picture {
    position: absolute;
    right: 0;
    top: 0;
    height: 100vh;
    z-index: -1;
}

.yellow-h3::selection {
    background: #442FE0;
}

.yellow-h3::-moz-selection {
    background: #442FE0;
}